*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  transition: all 0.3s;

  &:active {
    opacity: 0.5;
  }
}

.site-container {
  height: 100%;
  // overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  // margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

.title {
  font-size: 200px;
  line-height: 230px;
  font-weight: 500;
  text-transform: uppercase;
}

.main {
  padding-bottom: 120px;
  flex: 1 auto;
}

.section {
  margin-top: 120px;

  &__header {
    position: sticky;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(20px);
    z-index: 20;
    padding: 4px 0;
    transition: all 0.3s;

    &.fixed {
      background-color: #00000036;

      & h2 {
        color: #fff;
      }
    }
  }

  & h2 {
    font-size: 64px;
    line-height: 100%;
    color: var(--black-color);
    font-weight: 500;
    transition: all 0.3s;
  }

  &>div {
    margin-top: 40px;
  }
}

.all-link {
  width: fit-content;
  margin: 0 auto;
  margin-top: 70px;
  position: relative;

  &:hover {
    & .all-link__line {
      transform: translateY(-4px);
    }
  }

  & a {
    font-size: 22px;
    color: var(--black-color);
    font-weight: 300;
  }

  &__line {
    width: 100%;
    height: 1px;
    transition: all 0.3s;
  }
}

.first-block {
  margin-top: var(--header-height);
}

.arrow-up {
  cursor: pointer;
  z-index: 80;
  position: fixed;
  bottom: 40px;
  right: 40px;
  width: 80px;
  height: 80px;
  overflow: hidden;
  border-radius: 50%;
  padding: 16px;
  transition: all 0.3s;
  opacity: 0;
  visibility: hidden;

  &.show {
    opacity: 1;
    visibility: visible;
  }

  & svg {
    width: 100%;
    height: 100%;
    stroke: #fff;
    fill: transparent;
  }
}


@include big-desktop {
  .all-link {
    margin-top: 50px;
  }

  .section {
    & h2 {
      font-size: 58px;
    }
  }
}

@include tablet {
  .title {
    font-size: 122px;
    line-height: 140px;
  }
}

@include small-tablet {
  .all-link {
    margin-top: 32px;

    & a {
      font-size: 18px;
    }
  }

  .arrow-up {
    width: 40px;
    height: 40px;
    padding: 10px;
    bottom: 20px;
    right: 20px;
  }

  .title {
    font-size: 40px;
    line-height: 46px
  }
  .section {
    margin-top: 80px;

    & h2 {
      font-size: 26px !important;
    }

    &>div {
      margin-top: 30px;
    }
  }

  .main {
    padding-bottom: 80px;
  }
}
